@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=VT323&display=swap");

body {
    font-family: "VT323",
    monospace;
}

body::-webkit-scrollbar {
    display: none;
}

@layer base {
    body {
        @apply dark:bg-gray-800 bg-white relative
    }
}